  methods:{
    readBilayoutType(){
      var ww= window.innerWidth;
      
      if(ww > 990){
        return pbi.models.LayoutType.Custom;
      }else{
        return pbi.models.LayoutType.MobilePortrait;
      }
      
    },
    initBi(){
        
      var HH = document.getElementById("content-div-home").offsetHeight;
      document.getElementById('powerBiIframe').setAttribute("style","height:"+HH+"px");
      var config = this.config;


      var reportContainer = document.getElementById('powerBiIframe');
      var report = powerbi.embed(reportContainer, config);
      report.on("loaded",function(){
          var iframeWindow =  document.getElementById('powerBiIframe').childNodes[0]; 
          iframeWindow.style.border = 'none';
          iframeWindow.style.paddingLeft = '20px';
      });
      report.on("rendered",function(){
        //PowerBI載入完成
      });
      report.on("error", function () {
        console.log("XXXX");
        
          // this.report.off("error");
      });

      // console.log(pbi.models.LayoutType.MobilePortrait);
          
          
      
    },
    nextBIdata(){
      this.pageBiDataStatus = this.pageBiDataStatus+1;
      if(this.pageBiDataType.length <= this.pageBiDataStatus){
        this.pageBiDataStatus=0;
      }
      let menu = document.getElementById('powerBiIframe');
      if(menu.children.length != 0){
        menu.removeChild(menu.childNodes[0]);
        powerbi.reset(menu);
      }
      this.initBi();
    },
    backBIdata(){
      this.pageBiDataStatus = this.pageBiDataStatus-1;
      if(this.pageBiDataStatus < 0){
        this.pageBiDataStatus=3;
      }
      this.initBi();
    }
  }
}
</script>

