<style scoped src="@/assets/css/page.css"></style>
<style scoped>
  .page404{
    text-align: center;
    padding: 15px;
    font-size: 6vw;
    width: 700;
  }
  #powerBiIframe iframe{
    border: none !important;
  }

  .changePowerBiPageDiv{
    position: absolute;
    z-index: 500;
    width: 100%;
    height: 50px;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,0.3);
    /* border-radius: 25px; */
    transition: 0.3s all;
    line-height: 50px;
  }

  .changePowerBiPageDiv .left-div,
  .changePowerBiPageDiv .right-div{
    position:absolute;
    z-index: 0;
    top: 0;
    width: 50px;
    height: 50px;
    line-height: 50px;
    text-align: center;
  }
  
  .changePowerBiPageDiv .left-div{
    left: 0;
  }

  .changePowerBiPageDiv .right-div{
    right: 0;
  }

  .changePowerBiPageDiv .left-div img{
    transform: rotate(-90deg);
  }
  .changePowerBiPageDiv .right-div img{
    transform: rotate(90deg);
  }

  .changePowerBiPageDiv:hover{
    background: rgba(0,0,0,0.7);
    /* width: 200px;。 */
  }
</style>
